
.text {
    color: #000;
    font-size: 24px;
}

.btn1 {
    width: 200px;
    height: 40px;
    font-size: 16px;
    border-width: 1px;
    border-color: #346aff;
    border-radius: 10px;
    transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease;
    background-color: white;
    color: #346aff;
    text-align: center;
    text-justify: center;
    line-height: 42px;
    text-decoration: none;
}


.btn1:hover {
    color: white;
    border-color: #122c64;
    background-color: #346aff
}
.btn1:hover .btn1_txt {
    color: white;
}
.btn1_txt {
    color: #346aff;
    border-color: #122c64;
}

.btn2 {
    margin-top: 10px;
    width: 200px;
    height: 40px;
    font-size: 16px;
    border-width: 1px;
    border-color: white;
    border-radius: 10px;
    transition: background-color 0.3s ease, color 0.3s ease, border 0.3s ease;
    background-color: #346aff;
    color: white;
    line-height: 42px;
    text-decoration: none;
}

.btn2:hover {
    background-color: #122c64;
    border-color: #122c64;
}

.btn2:hover .btn2__txt {
    font-weight: bold;
}

.btn2__txt {
    color: white;
}
  